#speaker #page-header {
  box-shadow: inset 0 0 25px 7px rgba(0, 0, 0, 0.8);
  background-size: auto 100% !important;
  padding-bottom: 10px !important;
}
#speaker #page-header .photo {
  text-align: right;
}
#speaker #page-header .photo img {
  max-height: 250px;
  max-width: 100%;
  box-shadow: 0 0 30px 2px rgba(128, 128, 128, 0.5);
  border-radius: 15px;
}
#speaker #page-header .details {
  display: table;
  text-align: left;
  color: white;
}
#speaker #page-header .details h2 {
  color: inherit;
}
#speaker #page-header .details .function {
  font-size: 23px;
  margin-bottom: 5px;
}
#speaker #page-header .details .bio {
  font-size: 18px;
}
#speaker #page-header .details .social-profiles {
  display: table-row;
  height: 50px;
}
#speaker #page-header .details .social-profiles h2 {
  margin-top: 0;
  font-size: 16px;
}
#speaker #page-header .details .social-profiles a {
  font-size: 30px;
  padding-right: 10px;
  padding-bottom: 10px;
  float: left;
  color: #5d78bd;
}
#speaker #page-header .details .social-profiles a:hover {
  color: #fb953e;
}
#speaker #page-header .details .social-profiles .tooltip-arrow {
  border-bottom-color: whitesmoke;
}
#speaker #page-header .details .social-profiles .tooltip-inner {
  background-color: whitesmoke;
  color: black;
}
